.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Client {
  text-align: right;
  color: #0084ff;
}

.Server {
  text-align: left;
  color: #f1f0f0;
}

.ChatBox {
  margin: auto;
  width: 50%;
}

.ChatForm {
  width: 100%;
}

.Text {
  margin: auto;
}
